import { Box, Heading, Container, Text, Stack, Image } from "@chakra-ui/react";

export default function Home() {
  return (
    <>
      <Container maxW={"3xl"}>
        <Stack
          as={Box}
          alignContent="center"
          textAlign={"center"}
          spacing={{ base: 8, md: 14 }}
          py={{ base: 20, md: 36 }}
        >
          <Image
            w="200px"
            marginLeft={"38%"}
            src="https://avatars.githubusercontent.com/u/110049484?v=4"
            borderRadius={"full"}
          ></Image>
          <Heading
            fontWeight={600}
            fontSize={{ base: "2xl", sm: "4xl", md: "6xl" }}
            lineHeight={"110%"}
          >
            Charchit Khandelwal <br />
            <Text as={"span"} color={"green.400"}>
              Full Stack Web Developer
            </Text>
          </Heading>
          <Text color={"gray.500"}>
            A passionate Full Stack Web Developer, Dedicated to developing
            robust applications that interact with the part of web applications.
            Extensive expertise in website architecture, design, and web design,
            and web API designs. Problem-solving mindset with a goal to optimize
            my application for better stability and speed. Love to work in a
            team.
          </Text>
        </Stack>
      </Container>
    </>
  );
}
