import React from 'react'
import { Image,Heading,Grid } from '@chakra-ui/react'
const Skill = () => {
  return (
    <div>
        <Heading textDecoration="underline" marginTop="50px">My Skills</Heading>
        <Grid templateColumns="repeat(3,1fr)" gap="10px" marginTop="30px">
        <div><Image w="50px"
      src={process.env.PUBLIC_URL + '/Images/html.png'}
      alt='Green double couch with wooden legs'
      borderRadius='lg'
    /></div>
        <div><Image w="50px"
      src={process.env.PUBLIC_URL + '/Images/css.png'}
      alt='Green double couch with wooden legs'
      borderRadius='lg'
    /></div>
        <div><Image w="50px"
      src={process.env.PUBLIC_URL + '/Images/javascript1.jpg'}
      alt='Green double couch with wooden legs'
      borderRadius='lg'
    /></div>
        <div><Image w="50px"
      src={process.env.PUBLIC_URL + '/Images/react.png'}
      alt='Green double couch with wooden legs'
      borderRadius='lg'
    /></div>
        <div><Image w="50px"
      src={process.env.PUBLIC_URL + '/Images/node.png'}
      alt='Green double couch with wooden legs'
      borderRadius='lg'
    /></div>
        <div><Image w="50px"
      src={process.env.PUBLIC_URL + '/Images/TS.png'}
      alt='Green double couch with wooden legs'
      borderRadius='lg'
    /></div>
        <div><Image w="50px"
      src={process.env.PUBLIC_URL + '/Images/next.png'}
      alt='Green double couch with wooden legs'
      borderRadius='lg'
    /></div>
    </Grid>
    </div>
  )
}

export default Skill