import { Heading } from '@chakra-ui/react'
import React from 'react'
import styles from '../../styles/About.module.css'

const About = () => {
  return (
    <div className={styles.aboutSection}>
      <h2>About</h2>
      <div className={styles.childAboutSection}>
      <div>
        <img src="https://avatars.githubusercontent.com/u/110049484?v=4"alt="" />
      </div>
     <div>
        <h3>  A passionate Full Stack Web Developer, Dedicated to developing
            robust applications that interact with the part of web applications.
            Extensive expertise in website architecture, design, and web design,
            and web API designs. Problem-solving mindset with a goal to optimize
            my application for better stability and speed. Love to work in a
            team.
            </h3>
     </div>
     </div>
    </div>
  )
}

export default About